	module.exports = {"data":{"markdownRemark":{"html":"<h2>Test Driven Development</h2>\n<p>Test Driven Development (TDD) is one of Agile Software Development approaches. It is based on the concept that</p>\n<blockquote>\n<p>you must write a test case for your code even before you write the code</p>\n</blockquote>\n<p>It’s basically composed of 4 steps</p>\n<ul>\n<li>Write a test case</li>\n<li>See the test fail (Red)</li>\n<li>Make the test pass, comitting whatever crimes in the process (Green)</li>\n<li>Refactor the code to be up to standards (Refactor)</li>\n</ul>\n<p> These steps follow the principle of Red-Green-Refactor. Red-Green make sure that you write the simplest code possible to solve the problem while the last step makes sure that the code that you write is up to the standards.</p>\n<p>Each new feature of your system should follow the steps above.</p>\n<p><img src=\"http://www.agiledata.org/images/tddSteps.jpg\" alt=\"tdd flow\"></p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>Agile Data’s <a href='http://agiledata.org/essays/tdd.html' target='_blank' rel='nofollow'>Introduction to TDD</a></p>\n<p>Wiki on <a href='https://en.wikipedia.org/wiki/Test-driven_development' target='_blank' rel='nofollow'>TDD</a></p>\n<p>Martin Fowler <a href='https://martinfowler.com/articles/is-tdd-dead/' target='_blank' rel='nofollow'>Is TDD Dead?</a>\n(A series of recorded conversations on the subject)</p>\n<p> Kent Beck’s book <a href='https://www.amazon.com/Test-Driven-Development-Kent-Beck/dp/0321146530' target='_blank' rel='nofollow'>Test Driven Development by Example</a></p>\n<p>Uncle Bob’s <a href='http://blog.cleancoder.com/uncle-bob/2014/12/17/TheCyclesOfTDD.html' target='_blank' rel='nofollow'>The Cycles of TDD</a></p>","fields":{"slug":"/agile/test-driven-development/"},"frontmatter":{"title":"Test Driven Development"}}},"pathContext":{"slug":"/agile/test-driven-development/"}}

